.img-Wrapper {
    background-size: contain !important;
    margin-left: 0%;
    height: 450px;
    width: 450px;
}

@media screen and (min-width: 0px) and (max-width: 559px) {
    .img-Wrapper{
        width: 250px;
        height: 250px;
        
    }
}

@media screen and (min-width: 560px) and (max-width: 768px) {
    .img-Wrapper{
        width: 300px;
        height: 300px;
    }
}

@media screen and (min-width: 769px) and (max-width: 959px){
    .img-Wrapper{
        width: 400px;
        height: 400px;
    }
}

@media screen and (min-width: 960px) and (max-width: 1118px){
    .img-Wrapper{
        width: 500px;
        height: 500px;
    }
}

@media screen and (min-width: 1120px) and (max-width: 1536px){
    .img-Wrapper{
        width: 500px;
        height: 500px;
    }
}

@media screen and (min-width: 1538px) and (max-width: 1918px){
    .img-Wrapper{
        width: 500px;
        height: 500px;
    }
}

@media screen and (min-width: 1920px){
    .img-Wrapper{
        width: 600px;
        height: 600px;
    }
}